import {Dispatch} from 'redux';
import {productsAPI} from '../api/api';
import {AppThunk, RootState} from './store-redux';

//ACTION CREATORS
export const getCopchProducts = (copchItems: copchType[]) => {
    return {type: 'GET-COPCH', copchItems} as const
}
export const getPolyProducts = (polyItems: polyType[]) => {
    return {type: 'GET-POLY', polyItems} as const
}
export const getColdProducts = (coldItems: coldType[]) => {
    return {type: 'GET-COLD', coldItems} as const
}

export type ProductsActionTypes =
    ReturnType<typeof getCopchProducts>
    | ReturnType<typeof getPolyProducts>
    | ReturnType<typeof getColdProducts>

//THUNK CREATORS =======================================================

export const getCopchTC = (): AppThunk => async dispatch => { //async function Thunk
    try {
        const data = await productsAPI.getCopch() //wait for response
        dispatch(getCopchProducts(data)) //then dispatch AC to setState
    } catch (e) {
        throw new Error('fail')
    }
}
export const getPolyTC = (): AppThunk => async dispatch => { //async function Thunk
    try {
        const data = await productsAPI.getPoly() //wait for response
        dispatch(getPolyProducts(data)) //then dispatch AC to setState
    } catch (e) {
        throw new Error('fail')
    }
}
export const getColdTC = (): AppThunk => async dispatch => { //async function Thunk
    try {
        const data = await productsAPI.getCold() //wait for response
        dispatch(getColdProducts(data)) //then dispatch AC to setState
    } catch (e) {
        throw new Error('fail')
    }
}

//STATE =======================================================
// export type CopchProductType = {
//     id: number
//     name_prod: string
//     unit: string
//     price: string
//     copch_file: string
//     description?: string
// }
type ProductCommon = {
    id: number
    name_prod: string
    unit: string
    price: string
    description?: string
}

type copchType = ProductCommon & {
    copch_file: string
    //     1
};

type polyType = ProductCommon & {
    poly_file: string
    //     2
};

type coldType = ProductCommon & {
    cold_file: string
    //     3
};
export type ProductsType = {
    copch: copchType[]
    poly: polyType[]
    cold: coldType[]
}

// let initialState: Array<CopchProductType> = []
let initialState: ProductsType = {
    copch: [],
    poly: [],
    cold: []
};

//Reducer =======================================================

export const productsReducer = (state: ProductsType = initialState, action: ProductsActionTypes)
    : ProductsType => {

    switch (action.type) {
        case 'GET-COPCH':
            return {...state, copch: action.copchItems.map(el => ({...el}))}
        case 'GET-POLY':
            return {...state, poly: action.polyItems.map(el => ({...el}))}
        case 'GET-COLD':
            return {...state, cold: action.coldItems.map(el => ({...el}))}
        default:
            return state;
    }
}



